export const addMultiValueQuery = query => (key, value) => (Object.assign(Object.assign({}, query), { [key]: query[key] ? [...query[key], value] : [value] }));
export const addSingleValueQuery = query => (key, value) => (Object.assign(Object.assign({}, query), { [key]: value }));
export const removeMultiValueQuery = query => (key, value) => {
    if (query[key] === undefined) {
        return query;
    }
    const newSubQuery = query[key].filter(qval => qval !== value);
    if (newSubQuery.length === 0) {
        return Object.keys(query).reduce((acc, cur) => {
            if (cur === key) {
                return acc;
            }
            return Object.assign(Object.assign({}, acc), { [cur]: query[cur] });
        }, {});
    }
    return Object.assign(Object.assign({}, query), { [key]: newSubQuery });
};
export const removeSingleValueQuery = query => key => {
    return Object.keys(query).reduce((acc, cur) => {
        if (cur === key) {
            return acc;
        }
        return Object.assign(Object.assign({}, acc), { [cur]: query[cur] });
    }, {});
};
export const flatQueryValue = (name, value) => {
    if (typeof value === 'string') {
        return [{ name, value }];
    }
    return value.map(vl => ({ name, value: vl }));
};
