import { checkBoxLogic, deleteChip } from 'pages/costModels/components/logic/selectCheckbox';
import React from 'react';
export class WithPriceListSearch extends React.Component {
    constructor(props) {
        super(props);
        this.state = { filters: this.props.initialFilters };
        this.handleChange = this.handleChange.bind(this);
        this.onRemove = this.onRemove.bind(this);
        this.onSelect = this.onSelect.bind(this);
        this.onClearAll = this.onClearAll.bind(this);
    }
    handleChange(newSearch) {
        this.setState({
            filters: Object.assign(Object.assign({}, this.state.filters), newSearch),
        });
    }
    onClearAll() {
        this.setState({
            filters: Object.assign(Object.assign({}, this.state.filters), { metrics: [], measurements: [] }),
        });
    }
    onRemove(category, chip) {
        this.setState({
            filters: Object.assign(Object.assign({}, this.state.filters), { [category]: deleteChip(this.state.filters[category], chip) }),
        });
    }
    onSelect(key, value) {
        this.setState({
            filters: Object.assign(Object.assign({}, this.state.filters), { [key]: checkBoxLogic(this.state.filters[key], value) }),
        });
    }
    render() {
        return this.props.children({
            onClearAll: this.onClearAll,
            onRemove: this.onRemove,
            onSelect: this.onSelect,
            setSearch: this.handleChange,
            search: this.state.filters,
        });
    }
}
