import { TableVariant } from '@patternfly/react-table';
import { TableTemplate } from 'pages/costModels/components/tableTemplate';
import React from 'react';
import { useTranslation } from 'react-i18next';
import { formatCurrency } from 'utils/formatValue';
const cells = [
    'cost_models.table.tag_key',
    'cost_models.tag_value',
    'cost_models.rate',
    'description',
    'cost_models.default',
];
const translateRows = (t, rows) => {
    return rows.map(row => {
        const rowCells = row.cells.map(cell => {
            if (typeof cell === 'string') {
                return t(cell);
            }
            return cell;
        });
        return Object.assign(Object.assign({}, row), { cells: rowCells });
    });
};
const TagRateTable = ({ tagRates }) => {
    const { t } = useTranslation();
    const rows = tagRates.tag_values.map((tagValue, ix) => {
        return {
            cells: [
                ix === 0 ? tagRates.tag_key : '',
                tagValue.tag_value,
                formatCurrency(tagValue.value),
                tagValue.description,
                tagValue.default ? 'cost_models.yes' : 'cost_models.no',
            ],
        };
    });
    const translatedRows = translateRows(t, rows);
    return (React.createElement(TableTemplate, { "aria-label": `tag-table-rate-${tagRates.tag_key}`, borders: false, variant: TableVariant.compact, cells: cells, rows: translatedRows }));
};
export default TagRateTable;
